export default {
  formatDate (time) {
    const date = new Date(time)
    const months = [
      'Январь', 'Февраль', 'Март',
      'Апрель', 'Май', 'Июнь',
      'Июль', 'Август', 'Сентябрь',
      'Октябрь', 'Ноябрь', 'Декабрь'
    ]

    return `${months[date.getMonth()]} ${date.getDate()}, ${date.getFullYear()}`
  },

  formatNum (num) {
    if (num < 1000) {
      return num
    }

    if (num < 10000) {
      return `${String(num / 1000).slice(0, 4)}K`
    }

    if (num < 100000) {
      return `${String(num / 1000).slice(0, 5)}K`
    }

    if (num < 1000000) {
      return `${String(num / 1000).slice(0, 6)}K`
    }

    return '> 1M'
  }
}