<template>
  <section class="card shadow">
    <c-slider :items="data.images" />

    <article class="card__content">
      <h3 class="card__title">{{ data.title }}</h3>
      <c-tags class="card__tags" :items="data.tags" />
      <div class="card__text" v-html="data.text"></div>
    </article>

    <footer class="card__footer">
      <time class="card__date">{{ utils.formatDate(data.date) }}</time>

      <div class="card__buttons">
        <a class="card__button card__button--hovered" :href="data.github" target="_blank" v-if="data.github">
          <icon-github class="card__icon" />
          <span class="card__label">GitHub</span>
        </a>
        <a class="card__button card__button--hovered" :href="data.demo" target="_blank" v-if="data.demo">
          <icon-demo class="card__icon" />
          <span class="card__label">Demo</span>
        </a>
        <div class="card__button">
          <icon-views class="card__icon" />
          <span class="card__label">{{ utils.formatNum(data.views) }}</span>
        </div>
        <div class="card__button">
          <c-like :id="data.id" :amount="utils.formatNum(data.likes)" />
        </div>

        <!-- <div class="card__button">
          <c-share
            class="card__icon"
            :id="data.id"
            :amount="utils.formatNum(data.share)"
            :title="data.title"
            :link="$router.resolve({ name: 'view', params: { id: data.id } }).href"
            :image="data.images[0]"
            :tags="data.tags"
          />
        </div> -->
      </div>
    </footer>
  </section>
</template>

<script>
import utils from '@/utils'

export default {
  name: 'c-view',

  components: {
    cSlider: () => import('@/components/Slider'),
    cLike: () => import('@/components/Like'),
    // cShare: () => import('@/components/Share'),
    cTags: () => import('@/components/Tags'),
    iconGithub: () => import('mdi-vue/Github'),
    iconDemo: () => import('mdi-vue/LinkVariant'),
    iconViews: () => import('mdi-vue/EyeOutline')
  },

  props: {
    data: {
      type: Object,
      required: true
    }
  },

  data: () => ({
    utils
  })
}
</script>

<style scoped>
  .card {
    background: #fff;
    border-radius: 3px;
    overflow: hidden;
  }

  .card__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin: 0 25px 15px;
  }
  .card__buttons {
    display: flex;
    align-items: center;
  }
  .card__button {
    display: flex;
    align-items: center;
    text-decoration: none;
    color: hsl(var(--ui-primary));
  }
  .card__button + .card__button {
    margin-left: 20px;
  }
  .card__button--hovered {
    cursor: pointer;
    transition: color .2s;
  }
  .card__button--hovered:hover,
  .card__button--hovered:hover .card__icon {
    color: hsl(var(--ui-active));
  }
  .card__label {
    margin-left: 5px;
    font-size: 14px;
  }
  .card__icon {
    color: hsl(var(--ui-primary));
    width: 20px;
    padding: 5px;
  }

  .card__content {
    padding: 25px;
    margin: 0;
  }
  .card__date {
    font-size: 13px;
    color: hsl(var(--ui-primary-h), var(--ui-primary-s), calc(var(--ui-primary-l) + 20%))
  }
  .card__title {
    margin: 0 0 5px;
    font-size: 20px;
    color: hsl(var(--ui-primary));
  }
  .card__text {
    margin: 15px 0 0;
    font-size: 16px;
    color: hsl(var(--ui-primary));
  }
  .card__tags {
    font-size: 14px;
  }

  @media (max-width: 900px) {
    .card__content {
      padding: 15px;
    }
    .card__tags {
      font-size: 12px;
    }
    .card__footer {
      margin: 0 15px 15px;
    }
  }

  @media (max-width: 540px) {
    .card__label,
    .card__date {
      display: none;
    }
    .card__footer {
      justify-content: flex-end;
    }
  }
</style>